import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'filtroRecorList'
})

export class FiltroRecordatoriosLista implements PipeTransform{
    
    transform(lista: any [], tipoRetorno: string, texto: string) {
        texto = texto.toLowerCase();
        console.log(texto,lista)
        let listado = lista;
        if ( texto !== '') {
            listado = lista.filter((item) => {
                const titulo: string = (item.informacion.titulo).toLowerCase();
                const contenido: string = (item.informacion.contenido).toLowerCase();
                if (titulo.includes(texto) || contenido.includes(texto)) {
                    item.nada = false;
                    return item;
                }
            });
        }

        listado = listado.filter((item) => item.tipo === tipoRetorno);

        if (listado.length === 0) {
            return [{nada: true}];
        }
        return listado;
    }
}
