import { Pipe, PipeTransform } from '@angular/core';
import * as moment from 'moment';

@Pipe({
    name: 'convertFecha'
})

export class ConvertirUTCPipe implements PipeTransform {
    transform(value: any) {
        console.log(value, 'Fechs')
        return moment(value).format('DD-MM-YYYY hh:mm:a')
    }
}