import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'calculo'
})
export class ValoresPipe implements PipeTransform {

  transform(value: any[]): any {
      console.log(value, 'valor');
    let cantidad = 0;
    value.forEach((item) => {
        cantidad = cantidad + parseFloat(item.valoru);
    })

    console.log(cantidad)

    return cantidad;
  }

}
