import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filtroChat'
})


export class FiltroChat implements PipeTransform {
    
    transform(listado: any[], param: string) {
        param = param.toLowerCase();
        if (param === '') {
            return listado;
        } else {
            console.log(param)
            return listado.filter((item: string) => {
                const val: string = item;
                item = item.toLowerCase();
                if (item.includes(param)) {
                    item = val;
                    return val;
                }
            });
        }
        

    }
}
