import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'miFiltro'
})
export class MiFiltroPipe implements PipeTransform {

  transform(value: any, texto: string): any {
    texto = texto.toLowerCase();
    const array = value.filter((item: any) => {
      item.nombrecompleto = item.nombrecompleto.toLowerCase();
      return item.nombrecompleto.includes(texto);
    });

    return array;
  }

}
