import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'contar'
})
export class ContarPipe implements PipeTransform {

  transform(value: any[]): any {
    let cantidad = 0;
    value.forEach((item) => {
        cantidad = cantidad + parseFloat(item.cantidad);
    })

    return cantidad;
  }

}
