    subirArchivosChat(token, idCita, imagen, name?, mime?) {
        console.log('archivo', imagen)
        const options: FileUploadOptions = {
            fileKey: 'archivo',
            fileName: name,
            mimeType: mime,
            httpMethod: 'POST',
            headers: {
                'x-token': token,
                'x-idCita': idCita
            }
        }

        const fileTransfer: FileTransferObject = this.MmyTransfer.create();

        return from(fileTransfer.upload(imagen, `${URL_SERVER}/citas/chat/upload`, options)).pipe(
            catchError(error => {
                console.log(`No se pudo subir la imagen `, error);
                throw new Error(`No se pudo subir la imagen `);
            })
        )
        
    }


    descargarArchivosChat(file) {
       

        const fileTransfer: FileTransferObject = this.MmyTransfer.create();

        return from(fileTransfer.download(`${URL_SERVER}/Citas/${file}`, this.file.dataDirectory + 'MimotoMedia/' + file)).pipe(
            catchError(error => {
                console.log(`No se pudo subir la imagen `, error);
                throw new Error(`No se pudo subir la imagen `);
            })
        )
        
    }


