import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'colores'
})

export class ColoresPipe implements PipeTransform {

    transform(value: any) {
        if (value.tipo === 'dibujar') {
             return `background-color: ${value.dibujo.fondo}; color: ${value.dibujo.color};`;
          }
    }
}
