import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filtroRepo'
})


export class FiltroRecordatorio implements PipeTransform {
    
    transform(listado: any[], param: string, arr?: any[]) {
        param = param.toLowerCase();
        if (param === '') {
            return [];
        } else {
            return listado.filter((item: any) => {
                item.usuarioEmisor.checked = false;
                if (arr.length > 0) {
                    if (arr.find((person: any) => person.id === item.usuarioEmisor._id)) {
                        item.usuarioEmisor.checked = true;
                    }
                } 
                const val: string = item.usuarioEmisor.nombrecompleto;
                item.usuarioEmisor.nombrecompleto = item.usuarioEmisor.nombrecompleto.toLowerCase();
                if (item.usuarioEmisor.nombrecompleto.includes(param)) {
                    item.usuarioEmisor.nombrecompleto = val;
                    return val;
                }
            });
        }
        

    }
}
