import { Pipe, PipeTransform  } from '@angular/core';
import * as moment from 'moment';

@Pipe({
    name: 'cambioUTC'
})

export class CambioUTCaLocalPipe implements PipeTransform {

    transform(date: any) {
        let fecha = moment.utc(date).toDate();
        return moment(fecha).format('dddd, DD MMMM YYYY h:mm:a');
    }
}