import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'recortar'
})

export class RecortarTextoPipe implements PipeTransform {

    transform(value: string) {
        if (value.length >= 50) {
            return value.substr(0, 50) + '...';
        } else {
            return value;
        }
    }
}
